import React, { useState } from "react";

function PrivateKeyForm(props){
    const [type,settype] = useState('input')

    function showHide(e){
        e.preventDefault();
        e.stopPropagation();
        if(type === 'input'){
            settype('password')
        }
        else{
            settype('input')
        }
    }
    
    function OnchangeInput(e){
        props.setprivatekey(e.target.value)
        // console.log(props.privatekey)
    }
    
    return(
        <>
        <form className="littleform">
        <label className="password">{props.Title}
        <input type={type} className="password__input" value={props.privatekey} onChange={OnchangeInput} />
        <span className="password__show" onClick={showHide}>{type === 'input' ? 'Hide' : 'Show'}</span>
        </label>
        </form>
        </>
    )
}

export default PrivateKeyForm